.input-icon {
    width: 45px;
}

.input-icon img {

    width: 30px;

}

.App {
    display: flex;
    flex-direction: column;
    flex-basis: 100%
}

.todo-header {
    font-size: 55px;
    font-family: "Adobe clean";
    text-align: center;
}

.todo-body {
    display: flex;
    flex-direction: column;
    row-gap: 25px;
}

.todo-input {
    border: 1px solid rgba(128, 128, 128, 0.726);
    border-radius: 5px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    row-gap: 25px;

}

.todo-title {
    padding-left: 60px;
    position: relative;
    display: block;
    border-radius: 8px;
    overflow: hidden;

}

.todo-title input {
    padding: 15px 15px 10px;
    appearance: none;
    outline: none;
    font-size: 18px;
    border: 1px solid rgba(128, 128, 128, 0.726);
    border-left: none;
    width: 100%;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    display: block;

}

.input-icon {
    position: absolute;
    width: 60px;
    height: 100%;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    background-color: #24a3b6;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px 0
}

.todo-description {
    display: block;
}

.todo-description textarea {
    font-size: 18px;
    display: block;
    outline: none;
    resize: none;
    padding: 15px 15px 10px;
    width: 100%;
    height: 100px;
    overflow: auto;
    border: 1px solid rgba(128, 128, 128, 0.726);
    border-radius: 8px;

}

.btn {
    padding: 15px;
    position: relative;
}

.btn.active::before {
    position: absolute;
    content: "";
    border-radius: 50%;
    left: 50%;
    bottom: 7px;
    transform: translateX(-50%);
    background-color: #fff;
    width: 5px;
    height: 5px;

}

.btn-green {
    background-color: #24a3b6;
}

.add-task {}

.todo-filter {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    row-gap: 15px;

}

.todo-status {
    flex-basis: 100%;
    display: flex;
    justify-content: space-between;
}

.todo-status button {
    flex-basis: 25%;
}

.todo-status .filter-all {
    flex-basis: 10%;
}


.todo-sort {
    flex-grow: 1;
}

.sort-by-date {
    width: fit-content;
    margin-left: auto;
    display: flex;
    align-items: center;
    column-gap: 20px;
    cursor: pointer;
}

.sort-by-date img {
    transform: scale(1.0);
    transition: 0.5s;
}

.sort-by-date img:hover {
    transform: scale(1.1);

}

.sort-by-date img:first-child {
    width: 25px;
}

.sort-by-date img:last-child {
    width: 40px;
}

.todo-footer {
    margin-top: 25px;
}

.todo-delete {
    display: flex;
    justify-content: space-between;
    column-gap: 25px;
}

.btn-red {
    background-color: #d93649;
}

.delete-done,
.delete-all {
    flex-basis: 45%;
}

.sort-by,
.sort-by option {
    padding: 5px 10px;
    font-family: sans-serif;
}

.task-placeholder {
    font-size: 24px;
    text-align: center;
}