.task-item {
    padding: 0 15px 0 15px;
    border: 1px solid rgba(128, 128, 128, 0.726);
    border-radius: 8px;
    display: flex;
    flex-wrap: wrap;
    font-size: 18px;
    position: relative;
    transition: 0.5s;
}

.task-item.open {
    padding: 0 15px 0 15px;
}

.task-item>img {
    position: absolute;
    left: 18px;
    top: 18px;
    pointer-events: none;
    transition: 0.5s;
    transform: rotate(0);
}

.open.task-item>img {
    transform: rotate(180deg);
}

.row:nth-child(3) {
    padding-bottom: 15px;
    transition: 0.5s;


}

.open.task-item .row:nth-child(3) {

    margin-bottom: 10px;
}



.task-description {
    overflow: hidden;
    background: #e2e2e2;
    padding: 15px 15px 13px;
    border-radius: 10px;
    transition: 0.5s;
    word-break: break-word;
}

.row:nth-child(3) {
    max-height: 0;
    padding: 0;
}



.row {
    flex-basis: 100%;
    overflow: hidden;
}

.task-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.col:first-child {
    flex-grow: 1;
    max-width: 67%;

}

.task-title {
    padding: 15px 15px 13px 40px;
    cursor: pointer;
    flex-grow: 1;
    word-break: break-word;
}

.task-actions {
    display: flex;
    column-gap: 12px;
    align-items: center;
    line-height: 0;
    position: relative;
    padding-left: 20px;
}

.task-actions>* {
    position: relative;
    height: 30px;
    width: 30px;
    cursor: pointer;
}

.task-actions img:first-child {
    opacity: 1;
}

.task-actions img:last-child {
    opacity: 0;
}

.task-actions>*:hover img:first-child {
    opacity: 0;
}

.task-actions>*:hover img:last-child {
    opacity: 1;
}

.task-actions img {
    cursor: pointer;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    transition: 0.7s;
}

.task-check input {
    width: 20px;
    height: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    cursor: pointer;
}




.DateMarker {
    font-size: 16px;
    display: flex;
    justify-content: space-between;
    flex-grow: 1;
    padding-bottom: 5px;
    padding-top: 10px;
    border-top: 1px solid;

}

.createdAt,
.updatedAt {
    display: flex;
    column-gap: 10px;
}

.updatedAt {
    align-items: center;
}


.edit-task {
    position: relative;
    opacity: 1;
    transition: 0.5s;
    width: 20px;
    height: 20px;
    cursor: pointer;
}

.edit-task>* {
    transition: 0.5s;
}

.edit-task img:last-child {
    position: absolute;
    left: 0;
    opacity: 0;
    transition: 0.5s;
}

.edit-task:hover img:first-child {
    opacity: 0;
}

.edit-task:hover img:last-child {
    opacity: 1;
}

.task-status {
    position: static;
    width: fit-content;
    height: fit-content;
}

.task-status input {
    position: absolute;
    appearance: none;
}

.in-progress {
    padding: 18px 15px 15px;
    color: #ffc107;
    border: #ffc107 1px solid;
    border-radius: 5px;
    transition: 0.5s;
    border-color: #24a3b6;
    color: #24a3b6;

}

.task-status input:checked~.in-progress {
    color: #fff;
    background-color: #ffc107;
    background-color: #24a3b6;

}


.task-title.done {
    color: #d93649;
    text-decoration: line-through #d93649;
}

::-webkit-scrollbar {
    width: 7px;

}

::-webkit-scrollbar-track {

    background-color: #e2e2e2
}

::-webkit-scrollbar-thumb {
    background-color: #d93649;
    border-radius: 5px;
}