@font-face {
  font-family: "Adobe clean";
  src: url("./fonts/AdobeClean-Regular.woff2") format("woff2");
  font-weight: normal;
}

@font-face {
  font-family: "Adobe clean";
  src: url("./fonts/AdobeClean-BoldSemiCn.woff2") format("woff2");
  font-weight: 500;
}

@font-face {
  font-family: "Adobe clean";
  src: url("./fonts/AdobeClean-Bold.woff2") format("woff2");
  font-weight: bold;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Adobe clean";
  font-weight: bold;
}

ol,
ul,
li {
  list-style-type: none;
}

button {
  appearance: none;
  border: none;
  outline: none;
  color: #fff;
  padding: 14px 15px 10px !important;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  opacity: 1.0;
  transition: 0.5s;
}

button:hover {
  opacity: 0.7;
}

html,
body,
#root {
  height: 100%;
}

#root {
  max-width: 800px;
  padding: 50px 15px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: flex-start;

}

.hidden {
  display: none;
}